import { css, styled, variants } from '@lokalise/styled';

import type { AlertStatus } from './Alert';
import { spacing, typography, value } from '../../utils/theme';
import { IconButton } from '../IconButton';
import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';

interface ContainerProps {
	$styleStatus: AlertStatus;
}

export const Container = styled.div<ContainerProps>`
	display: grid;
	grid-template-columns: 1fr auto;
	column-gap: ${spacing(3)};
	align-items: flex-start;
	padding: ${spacing(4)};
	border-radius: ${value('radius.notification')};
	font-family: ${value('font.family.default')};
	${variants('$styleStatus', {
		success: css`
			background-color: ${value('color.background.success')};
		`,
		error: css`
			background-color: ${value('color.background.danger')};
		`,
		warning: css`
			background-color: ${value('color.background.warning')};
		`,
		info: css`
			background-color: ${value('color.background.info')};
		`,
	})};
`;

export const AlertLink = styled(Pressable)<PressableProps>`
	${typography('body.small.default')}

	color: ${value('color.text.default')};
	text-decoration: underline;
	min-width: fit-content;
	border: none;
	background: none;

	&:hover,
	&:active,
	&:active:hover {
		text-decoration: underline;
		color: ${value('color.text.link.hover')};
	}
`;

AlertLink.displayName = 'AlertLink';

export const Content = styled.div`
	${typography('body.small.default')}
	color: ${value('color.text.default')};
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	column-gap: ${spacing(1)};
`;

export const CloseButton = styled(IconButton)`
	margin-top: -${spacing(1)};
	margin-right: -${spacing(1)};
`;
